.search,
.content__search {
    padding: 0 10px;
    margin-left: 30px;
    border: 1px solid #bbc0c4;
    border-radius: 4px;
    line-height: 28px;
    width: 100%;
}

.wrap__search {
    position: relative;
}

.search-label::before,
.content__search-label::before {
    content: '';
    display: block;
    width: 15px;
    height: 15px;
    background: url(../../static/media/search.b63ee1c3.svg) no-repeat center;
    background-size: cover;
    position: absolute;
    top: 50%;
    left: 40px;
    transform: translateY(-50%);
}

@media (max-width: 600px) {
    .content__search {
        display: none;
    }

    .content__search-label::before {
        display: none;
    }
}

@media (max-width: 600px) {
    .wrap__search {
        display: none;
    }
}
.header {
  padding: 0 100px;
  line-height: 50px;
  background: #fafafb;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 0;
  border-top: 3px solid #f48024;
  box-shadow: 0px 0px 5px -2px rgba(0,0,0,0.75);
  font-family: 'Kalam', cursive;
}

.header__logo {
  text-decoration: none;
  line-height: 37px;
  background: #fafafb;
  border: 0;
  outline: none;
}

.sign-in {
    display: flex;
    line-height: 50px;
    background: #fafafb;
    justify-content: space-between;
    align-items: center;
    width: 210px;
}

.header__logo-icon {
  width: 30px;
  height: 30px;
}

.header__logo-text {
  padding-left: 10px;
  color: #000;
  font-size: 1.4em;
}

.header__photo {
  display: block;
}

.header__user {
  display: flex;
  align-items: center;
}

.header__login {
  color: #000;
  font-size: 1.4em;
  margin-left: 15px;
}

.header__photo {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  object-fit: cover;
}

.header__nav {
  position: relative;
  display: flex;
}

.nav__burger {
  background: #fafafb;
  border: 0;
  outline: 0;
  line-height: 10px;
}

.nav__list {
  position: absolute;
  right: 0;
  top: 37px;
  box-shadow: 0px 0px 5px -2px rgba(0,0,0,0.75);
  min-width: 200px;
  background: #fff;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

.nav__item {
  border-bottom: 1px solid #cbcfd3;
  cursor: pointer;
}

.nav__item:hover {
  background: #fafafb;
  border-left: 1px solid #f48024;
}

.nav__btn {
  display: block;
  width: 100%;
  text-decoration: none;
  color: #0077cc;
  border: 0;
  background: #fff;
  outline: none;
  line-height: 2.5em;
}

.nav__item:last-child {
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
}

.toggle-d {
  display: none;
}

.nav__burger-icon{
  fill: #848d94;
}

.burger-icon__toggle {
  fill: #000;
}

@media (max-width: 1400px) {
  .search {
    margin-left: 0;
  }

  .search-label::before {
    left: 10px;
  }
}

@media (max-width: 1050px) {
  .header {
    padding: 0 30px;
  }
}

@media (max-width: 900px) {
  .header__logo-text {
    display: none;
  }

  .search {
    width: 80%;
  }
}

@media (max-width: 660px) {
  .header__photo {
    display: none;
  }
}

@media (max-width: 510px) {
  .header__login {
    display: none;
  }
}

@media (max-width: 450px) {
  .search {
    width: 55%;
  }

  .header {
    padding: 0 30px;
  }
}

.main__nav {
    max-width: 250px;
    min-width: 150px;
    padding: 15px 0 10px 25px;
}

.main__nav-link {
    display: block;
    line-height: 1.8;
    font-size: 16px;
    color: #62696f;
    text-decoration: none;
}

.main__nav-item_active {
    background: #eff0f1;
    border-right: 2px solid #f47f25;
}

.main__nav-item {
    padding-left: 5px;
}

@media (max-width: 600px) {
    .main__nav {
        display: none;
    }
}
.filter__block {
    display: flex;
    border-left: 1px solid #d6d9dc;
    border-bottom: 1px solid #d6d9dc;
    align-items: center;
}

.content__filter {
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.filter__btn,
.question__tag {
    line-height: 2em;
    padding: 0 10px;
    height: 34px;
}

.filter__btn {
    color: #747d84;
    border: 1px solid #747d84;
    border-right: 0;
    background: #fff;
}

.filter__btn:first-child {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

.filter__btn:last-child {
    border-right: 1px solid #747d84;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.filter__btn-active {
    color: #000;
    background: #e5e6e8;
}

@media (max-width: 600px) {
    .content__filter {
        margin: auto;
    }
}
.content {
    padding: 15px 80px 0 0;
}

.list__question {
    padding: 25px 50px 15px 10px;
    display: grid;
    grid-template-columns: -webkit-max-content 1fr;
    grid-template-columns: max-content 1fr;
    border-left: 1px solid #d6d9dc;
    border-bottom: 1px solid #d6d9dc;
}

.rating__number {
    font-size: 1.5em;
}

.rating__number,
.rating__text {
    color: #6a737b;
    text-align: center;
    max-width: 150px;
    min-width: 120px;
}

.rating__text {
    margin-bottom: 20px;
    font-size: 0.9em;
}

.question__title {
    text-decoration: none;
    color: #0395fe;
}

.question__text {
    line-height: 1.5em;
    text-align: justify;
    max-height: 147px;
    overflow-y: hidden;
}

.question__text_full {
    line-height: 1.5em;
    text-align: justify;
    margin-bottom: 25px;
}

.question__title,
.question__text {
    margin-bottom: 15px;
}

.question__tags {
    display: flex;
}

.question__tag {
    background: #e1ecf4;
    color: #3e77a0;
    border: 0;
    border-radius: 4px;
    margin-right: 15px;
}

.question__infor {
    margin-left: auto;
}

.question__date {
    color: #868f95;
    font-size: 0.9em;
}

.question__author {
    color: #0077cc;
    margin-top: 10px;
}

.content__header {
    display: flex;
    justify-content: space-between;
}

.btn__ask {
    line-height: 2em;
}

.btn__ask,
.btn__sign-in,
.btn__sign-out {
    padding: 0 13px;
    display: block;
    color: #fff;
    background-color: #0095ff;
    box-shadow: inset 0 1px 0 0 rgba(255,255,255,0.4);
    border: 1px solid transparent;
    border-radius: 3px;
    font-size: 14px;
    text-align: center;
}

.btn__sign-in {
    width: 150px;
    line-height: 28px;
    margin-left: 10px;
}

.btn__sign-out {
    line-height: 28px;
    margin-left: 10px;
}

.b-top {
    border-top: 1px solid #d6d9dc;
    margin-top: 50px;
}

.d-none {
    display: none;
}

.list__question code,
.list__question pre {
    overflow: auto;
    white-space: pre-wrap;
}

.list__question img {
    max-width: 300px;
    max-height: 200px;
}

.question__describe {
    overflow-x: auto;
}

@media (max-width: 900px) {
    .list__question {
        grid-template-columns: 1fr;
        padding: 25px 10px 15px 10px;
    }

    .question__rating {
        display: none;
    }

    .user {
        width: 50%;
    }

    .user:not(:nth-child(3n)) {
        margin-right: 0;
    }

    .content {
        padding: 15px 50px 0 0;
    }
}

@media (max-width: 550px) {
    .content {
        padding: 15px 5px 0 0;
    }

    .btn__ask {
        margin: auto;
    }

    .content__header {
        display: block;
    }
}

.users__list {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 30px;
    border-left: 1px solid #d6d9dc;
    border-bottom: 1px solid #d6d9dc;
}

.user {
    width: 30%;
    display: flex;
    margin-bottom: 30px;
    box-sizing: border-box;
}

.user:not(:nth-child(3n)) {
    margin-right: 5%;
}

.user__icon {
    width: 48px;
    height: 48px;
}

.user__describe {
    margin-left: 9px;
    line-height: 1.2em;
}

.user__title {
    color: #0077cc;
    font-size: 14px;
}

.user__info,
.user__number {
    font-size: 12px;
    margin-bottom: 2px;
    color: #6a737c;
}

.user__number {
    font-weight: bold;
}

.user__skils {
    color: #0077cc;
}

@media (max-width: 900px) {
    .user {
        width: 50%;
    }

    .users__list {
        padding: 20px 10px 20px 20px;
    }

    .user:not(:nth-child(3n)) {
        margin-right: 0;
    }
}

@media (max-width: 600px) {
    .user {
        width: 100%;
    }
}
.tag {
    width: 24%;
    border: 1px solid #d6d9dc;
    padding: 10px;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 1%;
}

.tag:not(:nth-child(4n)) {
    margin-right: 1%;
}

.tags__list {
    display: flex;
    flex-wrap: wrap;
    padding: 20px;
    border-left: 1px solid #d6d9dc;
    border-bottom: 1px solid #d6d9dc;
}

.tag__title {
    margin-bottom: 12px;
    color: #0064bd;
    background: #cfeafe;
    text-align: center;
    border-width: 1px;
    border-radius: 3px;
    padding: 5px 8px;
}

.tag__describe {
    color: #3c4146;
    font-size: 13px;
    margin-bottom: 10px;
}

.tag__amount {
    color: #848d95;
}

.tag__title,
.tag__amount {
    font-size: 12px;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
}

.tag__title,
.tag__amount,
.tag__describe {
    line-height: 1.4em;
}

@media (max-width: 900px) {
    .tag:not(:nth-child(4n)) {
        margin-right: 0;
    }
    
    .tag {
        width: 50%;
    }

    .tags__list {
        padding-right: 0;
    }
}

@media (max-width: 600px) {
    .tag {
        width: 100%;
    }

    .tags__list {
        padding: 20px;
    }
}
.profile__person {
    display: flex;
}

.profile__icon {
    width: 300px;
    height: auto;
}

.profile__name {
    color: #1E4F93;
}

.profile__name,
.profile__city {
    line-height: 1.5em;
    font-size: 25px;
}

.profile {
    padding: 30px;
}

.profile__description {
    margin-left: 70px;
}

.nav__item-profile:not(:last-child) {
    margin-right: 15px;
}

.nav__item-profile {
    display: block;
}

.nav__link-profile {
    text-decoration: none;
    font-size: 15px;
    color: #666;
    display: block;
    line-height: 27px;
}

.nav__link-profile_active {
    color: #F35D5F;
    border-bottom: 2px solid #F35D5F;
}

.profile__nav {
    margin: 50px 0 40px;
} 

.profile__info {
    display: flex;
    justify-content: space-between;
}

.profile__info-img {
    max-width: 400px;
}

.profile__title {
    font-weight: bold;
    font-size: 1.5em;
    line-height: 2em;
}

.info-list__item {
    line-height: 1.5em;
    color: rgb(70, 69, 69);
}

.profile__text {
    line-height: 1.5em;
}

.profile__text_bold {
    font-weight: bold;
}

.profile__link {
    margin-bottom: 15px;
    display: block;
}

.profile__nav-list {
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-bottom: 1px solid #E5E5E5;
}

.toggle-nav {
    display: flex;
}

.profile__info-list {
    margin-top: 30px;
}

.toggle {
    display: none;
}

@media (max-width: 1100px) {
    .toggle {
        display: block;
    }

    .profile__nav-list {
        flex-direction: column;
    }

    .toggle-nav {
        display: none;
    }

    .nav__link-profile {
        width: 100%;
    }

    .nav__item-profile:not(:last-child) {
        margin-right: 0;
    }

    .profile__title {
        margin-top: 40px;
    }

    .nav__burger {
        margin-top: 15px;
        display: block;
    }
    
    .profile__nav {
        margin: 0;
        position: absolute;
    }
}

@media (max-width: 1000px) {
    .profile__info-img {
        max-width: 350px;
    }
}

@media (max-width: 900px) {
    .profile__description {
        margin-left: 20px;
    }

    .profile__icon {
        width: 200px;
    }
}

@media (max-width: 850px) {
   .profile__info {
        flex-direction: column;
    }
}

@media (max-width: 700px) {
    .profile__description {
        margin-left: 20px;
    }
}

@media (max-width: 600px) {
    .profile__info-img {
        margin: 30px auto 0;
    }
}

@media (max-width: 500px) {
    .profile__person  {
        flex-direction: column;
        align-items: center;
    }

    .profile {
        padding: 20px;
        width: 100%;
    }
}

.answers__amount {
    padding: 10px 30px;
    font-weight: bold;
    font-size: 1.3em;
}

.answer__write {
    border: 1px solid #bbc0c4;
    width: 60%;
    margin: 20px 0;
}

.answers__title {
    padding: 20px 30px 5px;
    font-weight: bold;
}

@media (max-width: 1000px) {
    .answer__write {
        width: 80%;
    }
}
.footer {
    margin-top: 40px;
    background: #242729;
    position: relative;
    box-shadow: 0px 0px 5px -2px rgba(0,0,0,0.75);
    border-bottom: 3px solid #f48024;
}

.footer__text {
    padding: 30px;
    color: #848d95;
}
